<template>
  <div style="display: none"></div>
</template>

<script>
import striptags from 'striptags'

export default {
  props: [ 'title', 'description', 'image' ],
  data () {
    return {
      t: '',
      dt: '',
      d: 'This is a place for words.',
      i: './static/poster.jpg',
      k: 'SOE,Read,Olafur Eliasson,Studio Olafur Eliasson,words'
    }
  },
  beforeMount () {
    if (this.title && this.title !== '') {
      this.t = this.title
      this.dt = 'SOE | ' + this.title
    } else {
      this.dt = 'SOE Read | Studio Olafur Eliasson'
    }
    if (this.description && this.description !== '') {
      this.d = striptags(this.description)
    }
    if (this.image && this.image !== '') {
      this.i = this.image
    }
  },
  head: {
    title () {
      if (this.t === null || this.t === '') {
        this.t = 'SOE Read'
        return {
          inner: 'SOE Read',
          complement: 'Studio Olafur Eliasson'
        }
      } else {
        return {
          inner: 'SOE Read',
          complement: this.t
        }
      }
    },
    meta () {
      return [
        { n: 'keywords', c: this.k },
        { n: 'description', c: this.d },
        // Twitter
        { n: 'twitter:title', c: this.dt },
        { n: 'twitter:description', c: this.d },
        // Google+ / Schema.org
        { ip: 'name', c: this.dt },
        { ip: 'description', c: this.d },
        // Facebook
        { p: 'og:image', c: this.i },
        { p: 'og:title', c: this.dt },
        { p: 'og:url', c: window.location.href },
        { p: 'og:site_name', c: 'SOE Read' },
        { p: 'og:type', c: 'video.other' }
      ]
    },
    link: [
      { r: 'icon', h: '/static/favicon.png', sz: '32x32', t: 'image/png' }
    ]
  }
}
</script>   


