import Vue from 'vue'
import Frontpage from './components/Frontpage'

import VueRouter from 'vue-router'
Vue.use(VueRouter)

var router = new VueRouter({
  mode: 'history',
  routes: [
    { path: '/', component: Frontpage },
    { path: '/article/:id', component: Frontpage },
    { path: '*', redirect: '/' }
  ]
})

export default router



