<template>
  <div>
    <head-meta :title="'Home'"></head-meta>

    <global-header :fontData=fontData></global-header>

    <ul class="article-list">
      <!-- SHOW ALL ARTICLES WITHOUT MATCHING TAGS AT END -->
      <li v-for="(item, i) in items" :key='i' 
        v-on:mouseover="showIndex=i" 
        v-on:mouseleave="showIndex=null" 
        onselectstart='return false;'
        v-on:click="toggleContent(i, item)">
          <div class='article-author'>{{ item.author }} -</div>
          <div class='article-title'>{{ item.title }}</div>
          
          <div class="item-tags" v-bind:class="{ 'large-tags': contentIndex == i }">
            <div>(</div>
            <div v-for="(tag, j) in item.tags" :key='j' 
              v-on:click="toggleTag($event, tag)">
              <!--<div v-if="j!=0">,</div>-->
              <div class='item-tag'
              v-bind:class="{ 'selected-tag': selectedTags.indexOf(tag) != -1 }">
              {{ tag }} 
              </div>
            </div>
            
            <div>)</div>
          </div>
          
          <div class='read-tag' v-show="showIndex == i">
            <div v-if="contentIndex!=i">+</div>
            <div v-else>-</div>
          </div>
          <div class='content' v-show="contentIndex == i"
          v-bind:style="{ fontSize: fontData.size + 'px', color: fontData.color.hex, background: fontData.background.hex }">
            {{ item.content }}
          </div>
        </li>
    </ul>
  </div>
</template>
<script>
import HeadMeta from './HeadMeta'
import GlobalHeader from './GlobalHeader'

var tags = ['attractive', 'bald', 'beautiful', 'chubby', 'clean', 'dazzling', 'drab', 'elegant', 'fancy', 'fit', 'flabby', 'glamorous', 'gorgeous', 'handsome', 'long', 'magnificent', 'muscular', 'plain', 'plump', 'quaint', 'scruffy', 'shapely', 'short', 'skinny', 'stocky', 'ugly', 'unkempt', 'unsightly']

export default {
  components: { HeadMeta, GlobalHeader },
  name: 'Home',
  props: {
    msg: String
  },
  data () {
    return {
      fontData: {
        size: 20,
        color: {
          hex: '#000000'
        },
        background: {
          hex: '#ffffff'
        }
      },
      test: true,
      showIndex: null,
      contentIndex: null,
      items: [],
      tags: [],
      selectedTags: []
    }
  },
  created () {
    window.fontData = this.fontData
  },
  mounted () {
    this.tags = tags
    this.items = genItems(this.tags)
  },
  watch: {
    test: function (val) {
      console.log(val)
    },
    selectedTags: function (val) {
      this.sortItems()
    }
  },
  methods: {
    sortItems: function () {
      /*
      var nSelected = []
      for (var item in this.items) {
        var intersectingTags = this.selectedTags.filter(value => item.tags.indexOf(value) !== -1)
        nSelected.push([intersectingTags.length, item])
      }
      */
      let self = this
      this.items.sort(function (a, b) {
        var nTagsA = self.selectedTags.filter(value => a.tags.indexOf(value) !== -1).length
        var nTagsB = self.selectedTags.filter(value => b.tags.indexOf(value) !== -1).length
        return nTagsB - nTagsA
      })
      /*
      this.items = []
      for (var counted in nSelected) {
        this.items.push(counted[0])
      }
      */
    },
    toggleContent: function (i, item) {
      if (this.contentIndex === i) {
        this.contentIndex = null
      } else {
        this.contentIndex = i
        window.history.pushState('page2', 'Title', '/article/' + item.id)
      }
    },
    toggleTag: function (event, tag) {
      event.stopPropagation()
      if (this.selectedTags.indexOf(tag) === -1) {
        this.selectedTags.push(tag)
      } else {
        this.selectedTags.splice(this.selectedTags.indexOf(tag), 1)
      }
    },
    containsSelectedTags: function (item) {
      var intersectingTags = this.selectedTags.filter(value => item.tags.indexOf(value) !== -1)
      return intersectingTags.length === this.selectedTags.length
    }
  }
}
function randomUnicodeString (length) {
  return Array.from({length: length}, () => {
    // return String.fromCharCode(Math.floor(Math.random() * (65536)))
    return String.fromCharCode(Math.floor(Math.random() * (200)))
  }).join('')
}

function subset (arr, n) {
  if (n >= arr.length) {
    return arr.slice()
  }
  arr = arr.slice() // create sample arr
  var selection = []
  for (let i = 0; i < n; i++) {
    selection.push(arr.splice(Math.random() * arr.length | 0, 1)[0])
  }
  return selection
}

function genTags () {
  var tags = []
  for (let i = 0; i < 100; i++) {
    tags.push(randomUnicodeString((Math.random() * 10 + 3) | 0))
  }
  return tags
}

function genItems (tags) {
  var items = []
  for (let i = 0; i < 100; i++) {
    items.push({
      title: randomUnicodeString((Math.random() * 10 + 7) | 0),
      author: randomUnicodeString((Math.random() * 10 + 4) | 0),
      content: randomUnicodeString((Math.random() * 5000 + 10000) | 0),
      tags: subset(tags, ((Math.random() * (6)) + 1) | 0),
      id: i
    })
  }
  return items
}
</script>

<style lang="scss" scoped>

$main-indent: 15%;

.article-list {
  border-top: 2px dashed black;
  list-style-type: none;
  padding-left: 0;
}

.article-list li {
  border-bottom: 2px dashed black;
  text-align: left;
  padding-left: $main-indent;
  padding-right: $main-indent;
  padding-top: 15px;
  padding-bottom: 15px;
  cursor: pointer;
}

.item-tags {
  overflow: hidden;
}

.item-tags div {
  float: left;
}

.item-tag:hover {
  background: red;
  color: white;
}

.selected-tag {
  background: pink;
}

.article-author {
  font-size: 30px;
  font-family: serif;
}

.article-title {
  font-size: 40px;
}

.read-tag {
  float: right;
  font-size: 56px;
  padding-right: 30px;
  margin-top: -80px;
}

.large-tags {
  font-size: 30px;
}
</style>



