<template>
  <div>
    <div style="display: none"></div>
    <h1 id="banner">Read Things.</h1>
    <button id="menu-button" v-bind:class="{ 'clicked-menu-button': showMenu }" v-on:click="toggleMenu()"></button>
    <div id="main-menu" v-show="showMenu" v-on:click.self="toggleMenu()">
      <div class="menu-row" v-on:click.self="toggleMenu()">
        <div class="menu-item" v-bind:style="{ fontSize: fontData.size + 'px', color: fontData.color.hex, background: fontData.background.hex }">
          Font Font Font
        </div>
      </div>
      <div class="menu-row" v-on:click.self="toggleMenu()">
        <div class="menu-item">
          <div id='increase-font-size' class='button' v-on:click="decreaseFontSize($event)" onselectstart='return false;'>smaller</div>
          <div>&nbsp||&nbsp</div>
          <div id='decrease-font-size' class='button' v-on:click="increaseFontSize($event)" onselectstart='return false;'>HUGER</div>
        </div>
      </div>
      <div class="menu-row" v-on:click.self="toggleMenu()">
        <div id='color-picker-item' class="menu-item">
          <div id='color-picker' class='button' onselectstart='return false;' v-on:click="toggleShowColorPalette()">color</div>
          <div>&nbsp</div>
          <chrome v-model="fontData.color" @input="setColor" v-show="showColorPalette"></chrome>
        </div>
      </div>
      <div class="menu-row" v-on:click.self="toggleMenu()">
        <div class="menu-item">
          <div id='background-picker' class='button' onselectstart='return false;' v-on:click="toggleShowBackgroundPalette()">background</div>
          <div>&nbsp</div>
          <chrome v-model="fontData.background" @input="setBackground" v-show="showBackgroundPalette"></chrome>
        </div>
      </div>
      
      <div class="slider"></div>
    </div>
  </div>
  
</template>
<script
  src="https://code.jquery.com/jquery-3.3.1.min.js"
  integrity="sha256-FgpCb/KJQlLNfOu91ta32o/NMZxltwRo8QtmkMRdAu8="
  crossorigin="anonymous"></script>

<script>
import { Slider, Chrome } from 'vue-color'

export default {
  components: {
    Chrome,
    Slider
  },
  props: {
    fontData: Object
  },
  data () {
    return {
      showMenu: false,
      showColorPalette: false,
      showBackgroundPalette: false
    }
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  methods: {
    toggleMenu: function () {
      window.$('.vc-chrome-alpha-wrap').remove()
      window.$('.vc-chrome-fields-wrap').remove()
      window.$('.vc-chrome-color-wrap').remove()

      if (this.showMenu) {
        this.showMenu = false
        this.showColorPalette = false
        this.showBackgroundPalette = false
      } else {
        this.showMenu = true
      }
    },
    increaseFontSize: function (e) {
      e.preventDefault()
      this.fontData.size += 2
    },
    decreaseFontSize: function (e) {
      e.preventDefault()
      this.fontData.size -= 2
    },
    toggleShowColorPalette: function () {
      this.showColorPalette = !this.showColorPalette
    },
    toggleShowBackgroundPalette: function () {
      this.showBackgroundPalette = !this.showBackgroundPalette
    },
    setColor (val) {
      this.fontData.color = val
    },
    setBackground (val) {
      this.fontData.background = val
    }
  },
  head: {
    title () {
    },
    meta () {
      return [
      ]
    },
    link: [
    ]
  }
}
</script>   

<style lang="scss" scoped>

#menu-button {
  position: fixed;
  background: black;
  height: 50px;
  width: 50px;
  right: 50px;
  top: 50px;
  cursor: pointer;
  z-index: 2;
  outline: none;
}

#banner {
  padding-top: 50px;
  padding-left: 15%;
  text-align: left;
  font-size: 50px;
}

#main-menu {
  font-size: 30px;
  padding-top: 100px;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100vw; 
  height: 100vh;
  background: rgba(266, 266, 266, 0.95);
}

.button:hover {
  background: red;
  color: white;
}

.button:active {
  background: black;
}

.button {
  cursor: pointer;
}

.menu-row {
  text-align: center;
  width: 100vw;
  display:inline-block;
}

.menu-item {
  display:inline-block;
}

.menu-item>* {
  float: left;
}

.clicked-menu-button {
  background: white !important;
  border: solid black 5px;
}
</style>


